export default function AnalyticsOrderTimeline() {
  return (
    <Box>
      <Card sx={{ p: 2 }}>
        <Stack spacing={1}>
          <Stack direction="row" spacing={1}>
            <Icon
              icon="mdi:note-outline"
              style={{
                padding: 3,
                borderRadius: '50%',
                border: '1px solid #919EAB32',
                fontSize: '24px',
                color: '#b72136'
              }}
            />
            <Stack>
              <Typography variant="subtitle2" sx={{ fontSize: '10px', color: '#212B36' }}>
                Notes
              </Typography>
              <Typography variant="body2" sx={{ color: '#637381', fontSize: '10px', mt: '-5px' }}>
                11:46 AM | Vishnu
              </Typography>
            </Stack>
          </Stack>
          <Stack>
            <Avatar
              alt="Remy Sharp"
              src="/static/mock-images/products/product_10.jpg"
              style={{
                width: '66px',
                height: '32px',
                borderRadius: 0
              }}
            />
          </Stack>
        </Stack>
      </Card>
    </Box>
  );
}
