import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
// material
import { Card, Typography, CardHeader, CardContent, Box, Stack } from '@mui/material';
import { Timeline, TimelineDot, TimelineItem, TimelineContent, TimelineConnector, TimelineSeparator } from '@mui/lab';
// utils
import { fDateTime } from '../../../utils/formatTime';
import mockData from '../../../utils/mock-data';
import Label from '../../Label';

// ----------------------------------------------------------------------

export default function AnalyticsOrderTimeline() {
  return (
    <Box>
      <Card sx={{ p: 2 }}>
        <Stack spacing={1}>
          <Stack direction="row" spacing={1}>
            <Icon
              icon="ic:outline-keyboard-voice"
              style={{
                padding: 3,
                borderRadius: '50%',
                border: '1px solid #919EAB32',
                fontSize: '24px',
                color: '#b72136'
              }}
            />
            <Stack>
              <Typography variant="subtitle2" sx={{ fontSize: '10px', color: '#212B36' }}>
                Audio Notes
              </Typography>
              <Typography variant="body2" sx={{ color: '#637381', fontSize: '10px', mt: '-5px' }}>
                11:46 AM | Vishnu
              </Typography>
            </Stack>
          </Stack>
          <Stack direction="row" spacing={2}>
            <Stack direction="row">
              <Icon icon="iconoir:voice" width="20" height="23" />
              <Icon icon="iconoir:voice" width="20" height="23" />
              <Icon icon="iconoir:voice" width="20" height="23" />
            </Stack>
            <Stack direction="row" spacing={1} sx={{ display: 'flex', alignItems: 'center' }}>
              <Icon icon="clarity:play-solid" color="black" width="15" height="15" />
              <Icon icon="fontisto:pause" color="black" width="10" height="14" />
            </Stack>
          </Stack>
        </Stack>
      </Card>
    </Box>
  );
}
