import { Icon } from '@iconify/react';
import { Stack, Box, Typography, TextField, Card, Divider } from '@mui/material';

export default function IINoteEdit() {
  return (
    <Stack direction="column" spacing={2} sx={{ mt: 5 }}>
      <TextField
        id="outlined-multiline-static"
        label="Notes"
        multiline
        rows={5}
        defaultValue=""
        sx={{ width: '270px', borderColor: '#919eab' }}
      />
      <Card sx={{ p: 1.5 }}>
        <Stack
          direction="row"
          divider={<Divider orientation="vertical" variant="middle" flexItem />}
          spacing={2}
          justifyContent="center"
        >
          <Stack direction="row" spacing={1} sx={{ display: 'flex', alignItems: 'center' }}>
            <Icon icon="carbon:text-bold" color="#919eab" width="16" height="24" />
            <Icon icon="mdi:slash-forward" color="#919eab" width="16" height="24" />
            <Icon icon="carbon:text-underline" color="#919eab" width="16" height="24" />
          </Stack>
          <Stack direction="row" spacing={1} sx={{ display: 'flex', alignItems: 'center' }}>
            <Icon icon="ic:outline-keyboard-voice" color="#8c9094" width="21" height="21" />
            <Icon icon="entypo:attachment" color="#8c9094" width="18" height="19" />
          </Stack>
          <Stack direction="row" spacing={1} sx={{ display: 'flex', alignItems: 'center' }}>
            <Icon icon="codicon:list-ordered" color="#8c9094" width="21" height="16.07" />
            <Icon icon="ant-design:unordered-list-outlined" color="#8c9094" width="20.25" height="15" />
            <Icon icon="akar-icons:image" color="#8c9094" width="20" height="20" />
          </Stack>
        </Stack>
      </Card>
    </Stack>
  );
}
