import { format } from 'date-fns';
import { sentenceCase } from 'change-case';
import { Icon } from '@iconify/react';
// material
import { useTheme, styled } from '@mui/material/styles';
import { Card, Stack, Avatar, AvatarGroup, Divider, Badge, Typography, Box } from '@mui/material';
//
import Label from '../../Label';
import Scrollbar from '../../Scrollbar';
// ----------------------------------------------------------------------
const StyledBadge = styled(Badge)(({ theme }) => ({
  '& .MuiBadge-badge': {
    backgroundColor: '#44b700',
    color: '#44b700'
  }
}));

export default function BookingDetails() {
  const theme = useTheme();

  return (
    <>
      <Card sx={{ px: 3, py: 1.5, fontFamily: 'inter' }}>
        <Scrollbar>
          <Stack direction="row" alignItems="center" spacing={2} sx={{ minWidth: 700 }}>
            <Stack spacing={1}>
              <Typography variant="subtitle1" sx={{ fontSize: '14px', fontWeight: '500', lineHeight: '17px' }}>
                Mrs.Meena's order
              </Typography>
              <Typography
                variant="subtitle2"
                sx={{ color: '#637381', fontSize: '10px', fontWeight: '500', lineHeight: '10px' }}
              >
                id # 0011223344
              </Typography>
            </Stack>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Stack>
              <Typography variant="subtitle1" sx={{ fontSize: '12px', fontWeight: '500', lineHeight: '17px' }}>
                Items
              </Typography>
              <Stack direction="row" spacing={0.5}>
                <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar
                    alt="Remy Sharp"
                    src="/static/mock-images/products/product_3.jpg"
                    style={{
                      width: '32px',
                      height: '32px',
                      borderRadius: '12px'
                    }}
                  />
                </StyledBadge>
                <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar
                    alt="Remy Sharp"
                    src="/static/mock-images/products/product_1.jpg"
                    style={{
                      width: '32px',
                      height: '32px',
                      borderRadius: '12px'
                    }}
                  />
                </StyledBadge>
                <StyledBadge
                  overlap="circular"
                  anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
                  variant="dot"
                >
                  <Avatar
                    alt="Remy Sharp"
                    src="/static/mock-images/products/product_9.jpg"
                    style={{
                      width: '32px',
                      height: '32px',
                      borderRadius: '12px'
                    }}
                  />
                </StyledBadge>
              </Stack>
            </Stack>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Stack>
              <Typography variant="subtitle1" sx={{ fontSize: '12px', fontWeight: '500', lineHeight: '15px' }}>
                Status
              </Typography>
              <Box>
                <Label
                  variant=""
                  style={{
                    backgroundColor: '#FFF2E2',
                    color: '#F2C94C',
                    fontSize: '10px',
                    fontWeight: 'bold',
                    lineHeight: '13px'
                  }}
                >
                  {sentenceCase('pending')}
                </Label>
              </Box>
            </Stack>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Stack>
              <Typography variant="subtitle1" sx={{ fontSize: '12px', fontWeight: '500', lineHeight: '17px' }}>
                Follow up Task Date
              </Typography>
              <Stack direction="row">
                <Typography
                  variant="subtitle2"
                  sx={{
                    color: '#637381',
                    fontSize: '10px',
                    fontWeight: '500',
                    lineHeight: '10px',
                    display: 'flex',
                    alignItems: 'center'
                  }}
                >
                  Oct 25, 2021
                </Typography>
                <Label
                  variant="filled"
                  style={{
                    backgroundColor: '#FD938D',
                    fontSize: '10px',
                    fontWeight: '700'
                    // fontFamily: 'Be Vietnam'
                  }}
                >
                  {sentenceCase('2 Days Delay')}
                </Label>
              </Stack>
            </Stack>
            <Divider orientation="vertical" variant="middle" flexItem />
            <Stack direction="row" sx={{ flexGrow: 1 }} justifyContent="space-between">
              <Stack>
                <Typography variant="subtitle1" sx={{ fontSize: '12px', fontWeight: '500', lineHeight: '17px' }}>
                  Hosts
                </Typography>
                <AvatarGroup max={4}>
                  <Avatar
                    alt="My Avatar"
                    src="/static/mock-images/avatars/avatar_3.jpg"
                    style={{ width: '32px', height: '32px' }}
                  />
                  <Avatar
                    alt="My Avatar"
                    src="/static/mock-images/avatars/avatar_6.jpg"
                    style={{ width: '32px', height: '32px' }}
                  />
                  <Avatar
                    alt="My Avatar"
                    src="/static/mock-images/avatars/avatar_5.jpg"
                    style={{ width: '32px', height: '32px' }}
                  />
                </AvatarGroup>
              </Stack>
              {/* <Box sx={{ flexGrow: 1 }}></Box> */}
              <Stack>
                <Icon icon="mdi:chevron-right" style={{ color: '#916A00', transform: 'scale(3)', marginTop: '20px' }} />
              </Stack>
            </Stack>

            <Box></Box>
          </Stack>
        </Scrollbar>
      </Card>
    </>
  );
}
