import { Icon } from '@iconify/react';
import { Stack, Box, Typography } from '@mui/material';

export default function IIId() {
  return (
    <Stack direction="row" spacing={2}>
      <Box
        component="img"
        src="/static/mock-images/products/product_3.jpg"
        onClick={() => handleOpenLightbox()}
        sx={{ width: 64, height: 64, objectFit: 'cover', cursor: 'pointer', borderRadius: 1.5 }}
      />
      <Stack>
        <Typography variant="subtitle2">Necklace</Typography>
        <Typography variant="subtitle2" sx={{ color: '#637381', fontSize: '10px' }}>
          id # 0011223344
        </Typography>
        <Stack direction="row" spacing={1}>
          <Icon
            icon="mdi:note-outline"
            style={{
              padding: 3,
              borderRadius: '50%',
              border: '1px solid #919EAB32',
              fontSize: '24px',
              color: '#b72136'
            }}
          />
          <Icon
            icon="mdi:camera-outline"
            style={{
              padding: 3,
              borderRadius: '50%',
              border: '1px solid #919EAB32',
              fontSize: '24px',
              color: '#6C98F1'
            }}
          />
          <Icon
            icon="entypo:attachment"
            style={{
              padding: 3,
              borderRadius: '50%',
              border: '1px solid #919EAB32',
              fontSize: '24px',
              color: '#69BC8B'
            }}
          />
        </Stack>
      </Stack>
    </Stack>
  );
}
