import { Icon } from '@iconify/react';
import { Stack, Box, Typography } from '@mui/material';

export default function Hosts() {
  return (
    <Box>
      <Stack
        direction="row"
        spacing={2}
        sx={{
          border: '1px solid rgba(145, 158, 171, 0.24)',
          borderRadius: '8px',
          pl: 1,
          py: 0.7,
          pr: 3,
          display: 'flex',
          alignItems: 'center'
        }}
      >
        <Icon icon="whh:circleselect" color="#008060" />
        <Typography variant="subtitle2" sx={{ color: '#8C9196', fontSize: '13px' }}>
          Selected
        </Typography>
      </Stack>
    </Box>
  );
}
