import { Icon } from '@iconify/react';
// material
import { useTheme, styled } from '@mui/material/styles';
import { Card, Stack, Avatar, Divider, Badge, Typography } from '@mui/material';
// ----------------------------------------------------------------------

export default function UserInfo() {
  return (
    <>
      <Card sx={{ px: 3, py: 1 }}>
        <Stack spacing={1.5}>
          <Stack direction="row" spacing={3}>
            <Stack direction="row" spacing={3}>
              <Avatar
                alt="My Avatar"
                src="/static/mock-images/avatars/avatar_14.jpg"
                sx={{ width: '32px', height: '32px' }}
              />
              <Stack direction="column" spacing={0.5}>
                <Typography variant="subtitle1" sx={{ fontSize: '14px', fontWeight: '500', lineHeight: '17px' }}>
                  Mrs.Meena's order
                </Typography>
                <Typography
                  variant="subtitle2"
                  sx={{ color: '#637381', fontSize: '10px', fontWeight: '500', lineHeight: '10px' }}
                >
                  id # 0011223344
                </Typography>
              </Stack>
            </Stack>

            <Stack>
              <Icon
                icon="mdi:dots-vertical"
                style={{
                  paddingTop: '2px',
                  paddingBottom: '2px',
                  paddingLeft: '5px',
                  paddingRight: '5px',
                  fontSize: '30px',
                  color: '#212B36',
                  border: '1px solid #919EAB24'
                }}
              />
            </Stack>
          </Stack>

          <Stack direction="row" spacing={2.5}>
            <Icon
              icon="mdi:calendar-check"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#FFE7D9',
                fontSize: '20px',
                color: '#B72136'
              }}
            />
            <Icon
              icon="mdi:phone"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#FFF7CD',
                fontSize: '20px',
                color: '#FFC107'
              }}
            />
            <Icon
              icon="mdi:email"
              style={{
                padding: 3,
                borderRadius: '8px',
                backgroundColor: '#C8FACD',
                fontSize: '20px',
                color: '#00AB55'
              }}
            />
          </Stack>
        </Stack>
      </Card>
    </>
  );
}
