import { Card, Stack, Box, Avatar, AvatarGroup, Typography } from '@mui/material';
import { IIId, IISelected, IINoteEdit, IINotes, IIAudioNote, IIImageNote } from './';

export default function Hosts() {
  return (
    <Card sx={{ p: 4, mt: 2 }}>
      <Stack spacing={3}>
        <Stack direction="row" justifyContent="space-between">
          <IIId />
          <IISelected />
        </Stack>
        <Stack direction={{ xs: 'column', sm: 'row' }} justifyContent="space-between" spacing={3}>
          <IINoteEdit />
          <Stack spacing={2}>
            <IINotes />
            <IIAudioNote />
            <IIImageNote />
          </Stack>
        </Stack>
      </Stack>
    </Card>
  );
}
